import React, { Component } from 'react';
import { Container, Row } from "reactstrap";

//Import Components
import SectionTitle from "../../components/common/section-title";

//Import Team Box
import TeamBox from "./team-box";

//Import Images
import team1 from "../../assets/images/users/img-1.jpg";
import team2 from "../../assets/images/users/img-2.jpg";
import team3 from "../../assets/images/users/img-3.jpg";
import team4 from "../../assets/images/users/img-4.jpg";

class OurTeam extends Component {
    state = {
        members : [
            { img : team1, name : "Nizar Maalej", post : "Founder", desc : "description" },
            { img : team2, name : "Team Member", post : "Job Description", desc : "description" },
            { img : team3, name : "Team Member", post : "Job Description", desc : "description" },
            { img : team4, name : "Team Member", post : "Job Description", desc : "description" },
        ]
    }
    render() {
        return (
            <React.Fragment>
                <section className="section" id="team">
                    <Container>
                    <SectionTitle
                        title="Our Team"
                        subtitle="Minds"
                        desc="Texte"
                    />
                        <Row className="mt-5 pt-2">
                            <TeamBox members={this.state.members} />
                        </Row>

                    </Container>
                </section>
            </React.Fragment>
        );
    }
}

export default OurTeam;